import {CustomHttp} from "../services/custom-http.js";
import config from "../config/config.js";
import {Auth} from "../services/auth.js";
import {Modal} from "../services/modal";
import {CatchError} from "../services/catchError";

export class Form {

    constructor(page) {
        this.userFirstName = null;
        this.userLastName = null;
        this.email = null;
        this.password = null;
        this.passwordRepeat = null;
        this.page = page;

        const accessToken = localStorage.getItem(Auth.accessTokenKey);
        if (accessToken) {
            location.href = '#/main';
            return;
        }

        this.fields = [
            {
                name: 'email',
                id: 'email',
                element: null,
                regex: /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/,
                valid: false,
            },
            {
                name: 'password',
                id: 'password',
                element: null,
                regex: /^[a-zA-Z0-9+_@.-]*$/,
                valid: false,
            },
        ];

        if (this.page === 'signup') {
            this.fields.unshift(
                {
                    name: 'name',
                    id: 'userFirstName',
                    element: null,
                    regex: /^([А-ЯЁA-Z][а-яёa-z]+\s*)+$/,
                    valid: false,
                },
                {
                    name: 'lastName',
                    id: 'userLastName',
                    element: null,
                    regex: /^([А-ЯЁA-Z][а-яёa-z]+\s*)+$/,
                    valid: false,
                });
            this.fields.push(
                {
                    name: 'repeatPassword',
                    id: 'repeatPassword',
                    element: null,
                    regex: /^[a-zA-Z0-9+_@.-]*$/,
                    valid: false,
                })
        }
        const that = this;
        this.fields.forEach(item => {
            item.element = document.getElementById(item.id);
            item.element.onchange = function () {
                that.validateField.call(that, item, this);
            }
        });

        this.userFirstName = document.getElementById('userFirstName');
        this.userLastName = document.getElementById('userLastName');
        this.email = document.getElementById('email');
        this.password = document.getElementById('password');
        this.passwordRepeat = document.getElementById('repeatPassword');

        this.modalWindow = document.getElementById('modal');
        this.modalMessage = document.getElementById('modal-msg');
        this.modalClose = document.getElementById('modal-close');
        this.modalClose.onclick = function () {
            Modal.closeModal(that.modalWindow);
        }

        this.form = document.getElementById('form');

        this.link = document.getElementById('link');
        this.link.onclick = function () {
            that.followLinks(that.page);
        }

        this.actionElement = document.getElementById('action');
        this.actionElement.onclick = function () {
            that.processForm();
        }
    }

    validateField(field, element) {
        if (!element.value || !element.value.match(field.regex)) {
            field.valid = false;
            this.invalidMessage(element, field.valid);
        } else {
            field.valid = true;
            this.invalidMessage(element, field.valid);
        }
        this.validateForm();
    }

    validateForm() {
        const validForm = this.fields.every(item => item.valid);
        let result = {
            validForm: validForm,
        };
        if (this.page === 'signup') {
            this.checkPassword = this.password.value === this.passwordRepeat.value;
            if (!this.checkPassword) {
                const value = false;
                this.invalidMessage(this.password, value);
                this.invalidMessage(this.passwordRepeat, value);
            } else {
                const value = true;
                this.invalidMessage(this.password, value);
                this.invalidMessage(this.passwordRepeat, value);
            }
            result.checkPassword = this.checkPassword;
            this.validForm(result);
            return validForm;
        }
        result.checkPassword = true;
        this.validForm(result);
        return validForm;
    }

    async processForm() {
        if (this.validateForm()) {
            const email = this.fields.find(item => item.name === 'email').element.value;
            const password = this.fields.find(item => item.name === 'password').element.value;

            if (this.page === 'signup') {
                try {
                    const result = await CustomHttp.request(config.host + '/signup', 'POST', {
                        userFirstName: this.fields.find(item => item.name === 'name').element.value,
                        userLastName: this.fields.find(item => item.name === 'lastName').element.value,
                        email: email,
                        password: password,
                        passwordRepeat: this.fields.find(item => item.name === 'repeatPassword').element.value
                    });

                    if (result) {
                        if (result.error) {
                            console.log(result);
                            throw new Error("Error: " + result.error);
                        }
                    }
                } catch (error) {
                    CatchError.showError(error, this.modalWindow, this.modalMessage);
                    this.form.reset();
                    return console.log(error);
                }
            }
            try {
                const result = await CustomHttp.request(config.host + '/login', 'POST', {
                    email: email,
                    password: password
                });

                if (result) {
                    if (result.error || !result.accessToken || !result.userFirstName || !result.userLastName || !result.userEmail
                        || !result.userId) {
                        throw new Error(result.message);
                    }

                    Auth.setTokens(result.accessToken)
                    Auth.setUserInfo({
                        userFirstName: result.userFirstName,
                        userLastName: result.userLastName,
                        userId: result.userId,
                        userEmail: email,
                    })
                    location.href = '#/main';
                }
            } catch (error) {
                CatchError.showError(error, this.modalWindow, this.modalMessage);
                this.form.reset();
                return console.log(error);
            }
        }
    }

    invalidMessage(element, value) {
        if (element && !value) {
            let invalidMessageElement = (element.nextElementSibling).nextElementSibling;
            invalidMessageElement.style.cssText = 'display: inline-block;';
            element.style.cssText = 'border: 1px solid var(--bs-form-invalid-color);';
        } else {
            let invalidMessageElement = (element.nextElementSibling).nextElementSibling;
            invalidMessageElement.style.cssText = 'display: none;';
            element.style.cssText = 'border: 1px solid var(--bs-border-color);';
        }
    }

    validForm(obj) {
        if (obj.validForm && obj.checkPassword) {
            this.actionElement.removeAttribute('disabled');
        } else {
            this.actionElement.setAttribute('disabled', 'disabled');
        }
    }

    followLinks(page) {
        switch (page) {
            case 'signup':
                location.href = '#/login';
                break;
            case 'login':
                location.href = '#/signup';
                break;
            default: break;
        }
    }
}