import {CustomHttp} from "../services/custom-http.js";
import config from "../config/config.js";
import {Auth} from "../services/auth";
import {Modal} from "../services/modal";
import {CatchError} from "../services/catchError";


export class Main {

    constructor() {
        this.blocked = 'blocked';
        this.unlocked = 'active';
        this.deleted = 'deleted';
        this.userName = "";
        this.users = [];
        this.userNameElement = document.getElementById('username');
        const that = this;
        this.currentUser = Auth.getUserInfo();

        this.checkboxes = document.getElementsByClassName('input-checkbox');
        this.selectAllCheckbox = document.getElementById('selectAllCheckbox');
        this.selectAllCheckbox.onclick = function () {
            if (that.selectAllCheckbox.checked) {
                Array.from(that.checkboxes).forEach(el => {
                    if (el.checked) {
                        el.checked = false;
                    }
                    el.checked = true;
                    el.setAttribute('checked', 'checked');
                });
            } else {
                Array.from(that.checkboxes).forEach(el => {
                    if (!el.checked) {
                        el.checked = true;
                    }
                    el.checked = false;
                    el.removeAttribute('checked');
                });
            }
        }

        this.buttonBlockUser = document.getElementById('block');
        this.buttonBlockUser.onclick = function () {
            that.updateStatusUser(that.blocked);
        }

        this.buttonUnlockUser = document.getElementById('unlock');
        this.buttonUnlockUser.onclick = function () {
            that.updateStatusUser(that.unlocked);
        }

        this.buttonLogout = document.getElementById('logout');
        this.buttonLogout.onclick = function () {
            that.exitProfile();
        }

        this.buttonDeleteUser = document.getElementById('delete');
        this.buttonDeleteUser.onclick = function () {
            that.updateStatusUser(that.deleted);
        }

        this.modalWindow = document.getElementById('modal');
        this.modalMessage = document.getElementById('modal-msg');
        this.modalClose = document.getElementById('modal-close');
        this.modalClose.onclick = function () {
            Modal.closeModal(that.modalWindow);
        }

        this.showNameUser();
        this.init();
    }

    async init() {
        try {
            const result = await CustomHttp.request(config.host + '/users');
            if (result) {
                if (result.error) {
                    throw new Error(result.error);
                }
                this.items = result;
                this.items.find(item => {
                    if (item.email === this.currentUser.userEmail && item.status === this.blocked) this.exitProfile();
                })
                this.selectAllCheckbox.checked = false;
                this.items.forEach((item) => {
                    this.createItemInTable(item);
                })
            }
        } catch (error) {
            CatchError.showError(error, this.modalWindow, this.modalMessage);
            return console.log(error);
        }
    }

    createItemInTable(user) {
        const table = document.getElementById('table_body');
        if (table) {
            const tableRow = document.createElement('tr');
            tableRow.classList.add('table-row');
            if (tableRow) {
                const firstCell = document.createElement('td');
                const checkbox = document.createElement('input');
                if (checkbox) {
                    checkbox.classList.add('form-check-input', 'input-checkbox');
                    checkbox.setAttribute('type', 'checkbox');
                    checkbox.setAttribute('value', user.userId);
                    firstCell.appendChild(checkbox);
                    tableRow.appendChild(firstCell);
                }
                let secondCell = null;
                this.createHtmlElement(secondCell, tableRow, `${user.userFirstName} ${user.userLastName}`);
                let thirdCell = null;
                this.createHtmlElement(thirdCell, tableRow, user.email);
                let forthCell = null;
                this.createHtmlElement(forthCell, tableRow, user.date);
                let fifthCell = null;
                this.createHtmlElement(fifthCell, tableRow, user.status);
            }
            table.appendChild(tableRow);
        }
    }

    showNameUser() {
        const userData = Auth.getUserInfo();
        this.userName = `${userData.userFirstName} ${userData.userLastName}`;
        this.userNameElement.innerHTML = `Hello, ${this.userName}!`;
    }

    createHtmlElement(tableElement, parent, value) {
        tableElement = document.createElement('td');
        if (tableElement) {
            tableElement.innerHTML = value;
            parent.appendChild(tableElement);
        }
    }

    updateStatusUser(userStatus) {
        const usersForUpdateStatus = [];
        Array.from(this.checkboxes).forEach(el => {
            if (el.checked) {
                return usersForUpdateStatus.push(el.value);
            }
        });
        if (usersForUpdateStatus && usersForUpdateStatus.length > 0) {
            this.processStatus(usersForUpdateStatus, userStatus);
        } else {
            return false;
        }
    }

    async processStatus(usersForUpdateStatus, userStatus) {
        if (usersForUpdateStatus && usersForUpdateStatus.length > 0 && userStatus) {
            try {
                let result;
                if (userStatus === this.deleted) {
                    result = await CustomHttp.request(config.host + '/delete', 'DELETE', {
                        users: usersForUpdateStatus,
                    });
                } else {
                    result = await CustomHttp.request(config.host + '/update', 'POST', {
                        users: usersForUpdateStatus,
                        status: userStatus
                    });
                }
                if (result) {
                    if (result.error) {
                        throw new Error(result.message);
                    }

                    if (userStatus === this.deleted) {
                        if (usersForUpdateStatus.find(item => item === this.currentUser.userId)) this.exitProfile();
                    }
                    this.deletePreviousItems('table-row');
                    this.init();
                }
            } catch (error) {
                CatchError.showError(error, this.modalWindow, this.modalMessage);
                return console.log(error);
            }
        }
    }

    exitProfile() {
        Auth.removeTokens();
        localStorage.clear();
        location.href = '#/login';
    }

    deletePreviousItems(selector) {
        if (document.getElementsByClassName(selector)) {
            Array.from(document.getElementsByClassName(selector)).forEach(item => item.remove());
        }
    }
}