export class Modal {
    static showModal(modalWindow, modalMessage, msg) {
        if (!modalWindow || !modalMessage|| !msg) {
            return false;
        } else {
            modalWindow.style = 'display: block';
            modalMessage.innerHTML = msg;
            return true;
        }
    }

    static closeModal(modalWindow) {
        if (!modalWindow ) return false;
        modalWindow.style = 'display: none';
        return true;
    }
}